import vFooter from '../footer'
  export default {
    components: {
      vFooter
    },
    data() {
      return {
        swiperOption: {
          //autoplay: true,  // 
          speed: 1000,   //  
          direction: 'vertical', //   horizontal   Slides
          autoplayDisableOnInteraction: true, //swiperautoplay
          pagination: { //cssHTML
            el: '.swiper-pagination',
            clickable: true,
            paginationType: 'progress',
          },
          height: window.innerHeight, // 
          autoHeight: true,//truewrappercontainerslide
          slidesPerView: 1,//sliderslides(carousel)loop 'auto'slidesloop'auto'loopedSlides
          // swiperswiper
          mousewheel: true,//Swiperfalse
          mousewheelControl: true,//
          resistanceRatio: 0,//slide0
          onTransitionStart(swiper){
            console.log(swiper)
          }
        }
      }
    },
    computed : {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted () {
      // current swiper instance
      // swiper
      console.log('this is current swiper instance object', this.swiper)
      // this.swiper.slideTo(3, 1000, false)
    }
  }



